import * as React from 'react';
import {Label, LabelType, LabelVariant} from '@here/hds-label';
// eslint-disable-next-line no-new
new Label();

interface HDSLabelProperties extends React.HTMLAttributes<HTMLElement> {
  primaryLabel?: React.ReactNode;
  secondaryLabel?: React.ReactNode;
  type: LabelType;
  variant: LabelVariant;
  for?: string;
}

const HDSLabel = React.forwardRef((props: HDSLabelProperties, ref) => {
  const {children, primaryLabel, secondaryLabel, className, ...rest} = props;

  return (
    <hds-label ref={ref} {...(className && {class: className})} {...rest}>
      {primaryLabel ? <span>{primaryLabel}</span> : children}
      {secondaryLabel && <span slot="secondary">{secondaryLabel}</span>}
    </hds-label>
  );
});

HDSLabel.defaultProps = {
  variant: 'default',
  type: 'default',
};

export default HDSLabel;
